<template>
  <div class="Notes">
    <div class="empty_note">
      <button id="add_note" @click="Add">add</button>
    </div>
    <div class="Note" v-for="(note, index) in allNotes" :key="index">
      <h4>{{ note.title }}</h4>
      <ol>
        <li
          v-for="task in ShortTasksList(index)"
          :key="task.index"
          :class="{ completed: task.isCompleted }"
        >
          {{ task.text }}
        </li>
      </ol>
      <div
        class="div"
        v-if="HideTasks(index)"
        :class="{ hide_tasks: HideTasks }"
      >
        <span>...</span>
      </div>
      <div class="note_options">
        <button @click="ShowModalWindow(index)">Remove</button>
        <button @click="EditNote(index)">Edit</button>
      </div>
    </div>
    <ModalWindow
      v-if="ModalWindowVisibility"
      @hide="setModalWindowVisibility(false)"
    >
      <template v-slot:question>
        Are you sure you want to delete the note?
      </template>
      <template v-slot:actions>
        <div class="buttons">
          <button @click="Remove(ItemIndex)">Yes</button>
          <button @click="setModalWindowVisibility(false)">No</button>
        </div>
      </template>
    </ModalWindow>
  </div>
</template>

<script>
import ModalWindow from "./ModalWindow.vue";
import { mapGetters } from "vuex";
import { mapMutations } from "vuex";

export default {
  name: "Note",
  components: {
    ModalWindow,
  },
  computed: {
    ...mapGetters(["allNotes", "getIndex", "ModalWindowVisibility"]),
  },
  mounted() {
    document.addEventListener("keydown", (e) => {
      if (event.ctrlKey && event.key === "b") {
        this.undo();
        this.stateChange();
      }
    });
    document.addEventListener("keydown", (e) => {
      if (event.ctrlKey && event.key === "q") {
        this.redo();
        this.stateChange();
      }
    });
  },
  data() {
    return {
      ItemIndex: Number,
    };
  },
  props: {
    shortListLength: Number,
  },
  methods: {
    ...mapMutations([
      "saveNoteIndex",
      "holdNotes",
      "setModalWindowVisibility",
      "removeNote",
      "mutate",
      "undo",
      "redo",
      "stateChange",
      "editWindow",
    ]),
    Add() {
      this.editWindow(false);
      this.$router.push("/edit");
    },
    HideTasks(index) {
      if (this.allNotes[index].tasks.length > this.ShortTasksList(index).length)
        return true;
    },
    ShortTasksList(index) {
      return this.allNotes[index].tasks.slice(0, this.shortListLength);
    },
    Remove() {
      this.removeNote(this.getIndex);
      this.setModalWindowVisibility(false);
    },
    EditNote(index) {
      this.holdNotes();
      this.saveNoteIndex(index);
      this.editWindow(true);
      this.$router.push("/edit");
    },
    ShowModalWindow(index) {
      this.saveNoteIndex(index);
      this.setModalWindowVisibility(true);
    },
  },
};
</script>

