  width: 90%;
  margin-left: 5%;
  position: absolute;
  bottom: 2.5vh;
  text-align: center;
  display: flex;
  justify-content: space-between;
}

.empty_note {
  box-shadow: 0 15px 68px rgba(0, 0, 0, 0.33);
  border-radius: $note_border-radius;
}

